body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.button2{
  position: relative;
  box-shadow: border-box;
  height: 32px;
  border: 1px solid rgba(0,0,0,0.3);
  background: #fff;
  padding: 3px 10px;
  border-radius: 5px;
  margin:20px;
  overflow: hidden;

}

.button2:focus{
  outline: none;
  border: 1px solid black;
}

.button2 > .circle{
  position: absolute;
  border-radius: 50%;
  height: 10px;
  width: 10px;
  background: red;
  opacity: 0.5;
  display: inline-block;
  -webkit-animation: 1s big forwards;
          animation: 1s big forwards;
  z-index: 0;
}

.button2 > .value{
  position: relative;
  z-index: 1;
}

@-webkit-keyframes big{
  0%{
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100%{
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}

@keyframes big{
  0%{
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  100%{
    -webkit-transform: scale(10);
            transform: scale(10);
  }
}
